html {
  background-color: #181818;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Overpass';
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #181818;
  color: #ffffff;
}

.header {
  height: 270px;
  background-color: #181818;
  color: #ffffff;
  margin-bottom:30px;
}

.link {
  color: #8c8cb1;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 18px;
}

.headertext {
  font-size: 18px;
  color: #707070;
}

.bodytext {
  margin-bottom: -10px;
}

.link:hover {
  color: #ccccee;
}

.link:active {
  color: #ccccee;
}

.logo {
  width: 200px;
  margin-top: 50px;
}

.main {
  flex: 1;
  display: flex;
}

.sidebar {
  flex: 1;
  width: 250px; /* Adjust this value as needed */
  margin-left: 50px; /* Adjust this value as needed */
  display: flex;
  flex-direction: column;
  background-color: #181818;
  position: fixed;
  overflow: auto;
  height: 100vh;
  z-index: 999;
}

#wrapper {
  position: relative;
  margin: 0;
  width: 100%;
  overflow: hidden;
  opacity: 1;
  visibility: visible;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.mobileHeader {
  display: block;
  margin: 30px 10px 0;
  text-align: center;
  z-index: 999;
}

.mobileLogo {
  width: 200px;
}

.mobileLinks {
  padding: 0;
  margin: 25px 0 25px;
  list-style-type: none;
}

.headerLinks{
  padding: 0 3px;
  line-height: 16px;
  font-size: 15px;
  display: inline-block;
  cursor: pointer;
}

hr.custom-hr {
  height: 1px; /* Adjust the height as desired */
  background-color: #444444; /* Adjust the color as desired */
  border: none; /* Remove the default border */
  margin: 40px 0; /* Adjust the margin as desired */
  width: 100px;
}

hr.custom-hr2 {
  height: 1px; /* Adjust the height as desired */
  background-color: #444444; /* Adjust the color as desired */
  border: none; /* Remove the default border */
  margin: 40px 0; /* Adjust the margin as desired */
  width: 640px;
}

hr.custom-hr-mobile {
  height: 1px; /* Adjust the height as desired */
  background-color: #444444; /* Adjust the color as desired */
  border: none; /* Remove the default border */
  width: 100px;
}

.body {
  flex: 1;
  background-color: #181818;
  color: #ffffff;
}

.footer {
  height: 40px;
  margin-top: 40px;
  color: #494949;
  font-size: 12px;
  background-color: #181818;
  color: #535353;
}

.image-container {
  position: relative;
  display: inline-block;
}

.responsive-image {
  position: relative;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 5px;
  left: 0;
  right: 0;
  transition: opacity 0.3s ease;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  cursor: pointer;
}

.overlay:hover {
  opacity: 1;
}

.text {
  color: white;
  font-size: 26px;
  text-align: center;
}

.description{
  color: white;
  font-size: 16px;
  text-align: center;
}

.responsive-image:hover .image-overlay {
  opacity: 1;
}

.project-desc {
  background-color: #181818;
  display: inline  !important;
  color: #9e9e9e;
  text-align: left;
  max-width: 620px;
  width: 90vw;
}

@media (min-width: 769px){
  .project-desc {
    width: 100%;
    border-collapse: collapse;
    max-width: 700px;
    width: 55vw;
  }
}

@media (min-width: 1441px) {
  .project-desc {
    max-width: 700px;
    width: 32vw;
  }
}

.project-desc-wrapper {
  display: inherit;
}

h2 {
  margin-top: -30px;
  margin-bottom: -30px;
}

@media (min-width: 1441px) {
  .project-desc-wrapper {
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 670px;
    margin-top: 20px;
    max-width: 600px;
  }
}

.image-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 10px;
  padding: 20px;
  max-width: 820px; /* Adjust this value as needed */
  margin: 0;
  align-content: flex-start;
  justify-content: flex-start;
  background-color: #181818;
  overflow: auto; /* Enable scrolling within the image grid */
}


@media (min-width: 769px) {
  .image-grid {
    grid-template-columns: repeat(2, 1fr);
    margin-left: 300px;
  }
}

.project-image-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(1, minmax(200px, 1fr));
  gap: 10px;
  padding: 20px;
  max-width: 640px; /* Adjust this value as needed */
  margin: 0;
  align-content: flex-start;
  justify-content: flex-start;
  background-color: #181818;
}

@media (min-width: 769px) {
  .project-image-grid {
    grid-template-columns: repeat(1, minmax(200px, 1fr));
    margin-left: 300px;
  }
}

.grid-table {
  width: 100%;
  border-collapse: collapse;
  max-width: 620px;
  width: 90vw;
}

@media (min-width: 769px){
  .grid-table {
    width: 100%;
    border-collapse: collapse;
    max-width: 700px;
    width: 55vw;
  }
}

@media (min-width: 1441px) {
  .grid-table {
    max-width: 700px;
    width: 32vw;
  }
}

.left-column {
  width: 20%; /* Adjust the width as desired */
}

.right-column {
  width: 80%; /* Adjust the width as desired */
}

.grid-cell {
  padding: 10px;
  text-align: left;
  background-color: #242424;
  padding: 15px;
}

.alt {
  background-color: #181818;
}

