main {
    display: block;
    position: relative;
    max-width: 1650px;
    min-width: 500px;
    padding: 40px 40px 100px 290px;
    box-sizing: border-box;
}

@media (max-width: 768px) {
    main {
        padding: 0px 40px 100px 0px;
        min-width: 500px;
    }
  }

.about .block {
    border-bottom: solid 1px #2d2d2d;
    color: #b8b8b8;
    padding: 40px 50px 25px 0;
    min-height: 32px;
}

.about .block.info {
    position: relative;
    padding-right: 0;
}

@media only screen and (max-width: 768px)
{
    .about h5 {
        line-height: 24px;
        float: none;
        margin: 0;
        width: 100px;
        position: inherit;
        text-align: left;
        color: #555;
    }

    .about .row {
        position: relative;
        padding-left: 40px;
    }

    .about {
        padding: 0;
    }

    .about p {
        line-height: 24px;
        font-size: 15px;
        padding: 0 0 0px;
    }
}

@media only screen and (min-width: 769px)
{
    .about h5 {
        line-height: 24px;
        float: left;
        margin: 0 0 0 -50px;
        width: 100px;
        position: absolute;
        text-align: right;
        color: #555;
    }
    
    .about .row {
        position: relative;
        padding-left: 0px;
    }

    .about {
        padding: 0px 40px;
        max-width: 960px;
        box-sizing: border-box;
    }

    .about p {
        line-height: 24px;
        font-size: 15px;
        padding: 0 0 0px;
        margin-left: 100px;
    }
}

.degreeText {
    color: #ccccee;
    font-size: 24px;
}

.schoolText {
    font-size:18px;
}

.locationText {
    color: #727272;
    font-size:14px;
    margin-top: -5px;
}